<template>
	<section class="register">
		<div class="registerHead">
			<!-- <img src="../images/back_aw.png" @click='history'/> -->
			<img src="../images/zhuche.png"/>
		</div>
		<div class="loginForm">
			<div class="phone">
				<img src="../images/loginphone.png"/>
				<input v-model='phone' placeholder="请输入手机号" type="number" oninput="if(value.length>11)value=value.slice(0,11)"/>
			</div>
			<div class="smsCode">
				<img src="../images/yanzhengma.png"/>
				<input maxlength="4" type="text" placeholder="输入验证码" v-model='smsCode'/>
				<span @click='send'>
	    			<timer-btn class="btn btn-default" ref="btn" :second="60"></timer-btn>
	    		</span>
	    		<div v-show='boxType' class="forgetBox" :state='boxState' @click='box'></div>
			</div>
			
		</div>
		<div class="next">
			<mt-button type="danger" @click='login'>下一步</mt-button>
		</div>
	</section>
  
</template>
 merit: ' 
<script>
	import { Toast } from 'mint-ui'
	import { fetchSmsVCode, verifySms } from './../api/api'
export default {
  data () {
    return {
    	phone: '', // 手机号码
    	boxType: true, // 短信验证码状态
    	smsCode: '' // 短信验证码
    }
  },
  methods: {
  	history() { // 后退
		this.$router.push({path: '/login'})
	},
	box() { // 验证码状态
		var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
		if (phoneReg.test(this.phone)) {
    		
    	} else {
    		Toast({
				message: '请输入正确手机号码',
				position: 'bottom',
				duration: 2000
			});
    	}
	},
    send() { // 获取验证码
    	var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
    	if (phoneReg.test(this.phone)) {
    		let params = {
    			phone: this.phone,
    			type: 'REGISTER'
    		}
    		fetchSmsVCode(params).then(res => {
    			if (res.data.state === '10') {
    				Toast({
						message: res.msg,
						position: 'bottom',
						duration: 2000
					});
    			} else if (res.data.state === '20') {
    				Toast({
						message: res.data.message,
						position: 'bottom',
						duration: 2000
					});
    			} else if (res.code === 500) {
    				Toast({
						message: res.msg,
						position: 'bottom',
						duration: 2000
					});
    			}
    		})
    	} else {
    		Toast({
				message: '请输入正确手机号码',
				position: 'bottom',
				duration: 2000
			});
    	}
    },
    login() { // 下一步
    	var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
		if (phoneReg.test(this.phone)) {
			if (this.smsCode === '' || this.smsCode.length !== 4) {
				Toast({
					message: '请输入正确验证码',
					position: 'bottom',
					duration: 2000
				});
			} else {
				let params = {
					phone: this.phone,
					type: 'REGISTER',
					vcode: this.smsCode
				}
				verifySms(params).then(res => {
					if (res.data.state === '10') {
						this.$router.push({path: '/registerPass', query: {loginPhone: this.phone, code: this.smsCode}})
					} else if (res.data.state === '20') {
						Toast({
							message: res.data.message,
							position: 'bottom',
							duration: 2000
						});
					}
				})
			}
		} else {
			Toast({
				message: '请输入正确手机号码',
				position: 'bottom',
				duration: 2000
			});
		}
    }
  },
  computed: {
  	boxState() {
  		var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
  		if (phoneReg.test(this.phone)) {
  			this.boxType = false
  		} else {
  			this.boxType = true
  		}
  	}
  },
  components: {
		'timer-btn': require('../components/PlugInUnit/timerbtn.vue')
	}
}
</script>

<style lang="scss" rel="stylesheet/scss">
.register {
	flex:1;
	z-index: 11;
	background: white;
	
	.registerHead {
		position: relative;
		height: 2.6rem;
		width: 100%;
		
		// img:nth-child(1) {
		// 	width: 0.2rem;
		// 	margin-top: 0.4rem;
		// 	margin-left: 0.2rem;
		// }
		img {
			position: absolute;
			width: 1.2rem;
			top: 2rem;
			left: 50%;
			margin-left: -0.6rem;
		}
	}
	
	.loginForm {
		width: 100%;
		height: 2rem;
		position: relative;
		margin-top:1.4rem;
		
		.phone {
			position: relative;
			width: 70%;
			margin-left:15%;
			img {
				position: absolute;
				width: 0.25rem;
				top: 0.18rem;
			}
			input {
				font-size: 0.25rem;
				position: absolute;
				width: 80%;
				height: 0.6rem;
				left: 0.5rem;
				outline: none;
				border: 0;
				padding-left: 0.3rem;
				border-bottom: 0.01rem rgba(0,0,0,0.1) solid;
			}
		}
		.smsCode {
			position: relative;
			width: 70%;
			top: 0.38rem;
			margin-left:15%;
			
			img:nth-child(1) {
				position: absolute;
				width: 0.25rem;
				top: 1.2rem;
			}
			input {
				font-size: 0.25rem;
				position: absolute;
				width: 80%;
				height: 0.6rem;
				left: 0.5rem;
				top: 1rem;
				outline: none;
				border: 0;
				padding-left: 0.3rem;
				border-bottom: 0.01rem rgba(0,0,0,0.1) solid;
			}
			span:nth-child(3) {
				position: absolute;
				right: -0.4rem;
				top: 1.05rem;
				
				.btn {
					height: 0.5rem;
					width: 1.6rem;
					background: #FFFFFF;
					font-size: 0.22rem;
					color: #d84949;
					outline: none;
					-webkit-appearance: none;
					border: 0;
					float: right;
					position: absolute;
					right: 0.3rem;
					text-align: right;
				}
			}
			.forgetBox {
				position: absolute;
				width: 1.8rem;
				height: 0.6rem;
				background: rgba(0,0,0,0);
				right: -0.2rem;
				top: 0.9rem;
			}
		}
	}
	.next {
		.mint-button {
			width: 70%;
			height: 0.7rem;
			margin-left: 15%;
			margin-top: 1rem;
			border-radius: 0.5rem;
			font-size: 0.25rem;
		}
	}
}

</style>



